Variables */
/* =====================================================================================
Basic styles */
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "HaginCaps", "Helvetica", sans-serif;
  font-size: 18px;
  line-height: 18px;
  font-weight: 400;
  color: #817155; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

* {
  box-sizing: border-box; }

/* =====================================================================================
Container */
.container {
  max-width: 1366px;
  width: 100%;
  margin: 0 auto;
  padding-top: 0;
  padding-right: 42px;
  padding-bottom: 0;
  padding-left: 53px; }

/* =====================================================================================
Media max-width: 1150px*/
@media (max-width: 1150px) {
  .container {
    padding-right: 15px;
    padding-left: 15px; } }

/* =====================================================================================
Visually-hidden */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* =====================================================================================
Main-btn */
.main-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 240px;
  width: 100%;
  min-height: 60px;
  font-size: 24px;
  line-height: 30px;
  text-transform: uppercase;
  color: #baa480;
  border: 1px solid #baa480; }

.main-btn:hover,
.main-btn:focus {
  opacity: 0.8; }

.main-btn:focus {
  opacity: 0.5; }

/* =====================================================================================
Media max-width: 900px*/
@media (max-width: 900px) {
  .main-btn {
    max-width: 196px;
    width: 100%;
    min-height: 50px;
    font-size: 22px;
    line-height: 30px; } }

/* =====================================================================================
Btn */
.btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 273px;
  width: 100%;
  min-height: 60px;
  font-size: 24px;
  line-height: 30px;
  text-transform: uppercase;
  color: #817155;
  border: 1px solid #9b8869;
  transition: all linear 0.2s; }

.btn::before,
.btn::after {
  content: "";
  position: absolute;
  top: 30px;
  display: block;
  width: 100vw;
  height: 1px;
  background-color: #9b8869; }

.btn::before {
  right: 282px; }

.btn::after {
  left: 282px; }

.btn:hover,
.btn:focus {
  color: rgba(129, 113, 85, 0.7);
  border-color: #ccb999; }

.btn:active {
  color: rgba(129, 113, 85, 0.5); }

/* =====================================================================================
// Media max-width: 1500px*/
@media (max-width: 900px) {
  .btn {
    max-width: 196px;
    min-height: 51px;
    font-size: 22px;
    line-height: 30px; }
  .btn::before,
  .btn::after {
    top: 23px; }
  .btn::before {
    right: 203px; }
  .btn::after {
    left: 203px; } }

